@import url(https://fonts.googleapis.com/css2?family=Josefin+Sans:wght@400;500;600;700&display=swap);
.App {
  display: flex;
  flex-direction: column;
  align-items: center;
  grid-gap: 1em;
  gap: 1em;
  padding: 1.25em;
}

.canvasWrapper {
  border: 2px solid #b0bec5;
  border-radius: 10px;
  background-color: #37474f;
  opacity: 0.8;
  background-image: linear-gradient(#455a64 1px, transparent 1px), linear-gradient(to right, #455a64 1px, #37474f 1px);
  background-size: 20px 20px;
}

.canvasWrapperGameEnd {
  border: 2px solid #ff5252;
  border-radius: 10px;
  background-color: #37474f;
  opacity: 0.8;
  background-image: linear-gradient(#455a64 1px, transparent 1px), linear-gradient(to right, #455a64 1px, #37474f 1px);
  background-size: 20px 20px;
}

.mainBoardWrapper {
  display: flex;
  flex-direction: column;
  align-items: center;
  grid-gap: 1em;
  gap: 1em;
}

.mainCanvasWrapper {
  display: flex;
  flex-direction: column;
  align-items: center;
  grid-gap: 1em;
  gap: 1em;
}

.menu {
  display: flex;
  flex-direction: column;
  align-items: center;
  grid-gap: 1em;
  gap: 1em;
  border: 2px solid #42a5f5;
  border-radius: 8px;
  padding: 1.5em;
  width: 600px;
  line-height: 1.5em;
}

.menu-title {
  font-size: 2em;
  font-weight: bold;
  color: #90caf9;
}

.header {
  display: flex;
  justify-content: space-between;
  align-self: stretch;
  align-items: center;
  padding-top: 0.5em;
  padding-bottom: 0.5em;
  padding-left: 0.2em;
  padding-right: 0.2em;

  font-size: 1.2em;
}

.scoreWrapper {
  font-size: 2em;
  font-weight: 500;
}

.contentWrapper {
  display: flex;
  align-items: stretch;
  grid-gap: 1em;
  gap: 1em;
}

.rankingBoardWrapper {
  display: flex;
  flex-direction: column;
  align-self: stretch;
  border: 1px solid #42a5f5;
  border-radius: 8px;
  padding: 1.5em;
  line-height: 1.5em;

  font-weight: 700;
  color: #1e88e5;
}

.rankingContentWrapper {
  display: flex;
  flex-direction: column;
  grid-gap: 0.5em;
  gap: 0.5em;

  font-weight: 400;
  color: #18191a;
}

body {
  margin: 0;
  font-family: 'Josefin Sans', -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

